import { Block, Controller } from '@ali/4ever-cangjie';
import { isSubjectHeading, SubjectHeading } from '../utils';

/**
 * 查询节点所在的议题节点
 * @param controller
 * @param node
 * @returns
 */
export function getSubjectStartBlock(controller: Controller, node: Block) {
  // 议题节点没有父级议题
  if (isSubjectHeading(node)) {
    return node;
  }

  const { document } = controller.value;
  const path = document.getPath(node.key);
  if (!path) {
    return null;
  }
  const [index] = path;

  // 查询第一个议题节点返回
  let subject: SubjectHeading | null = null;
  let i = index - 1;
  while (i >= 0) {
    const prev = document.nodes[i];
    if (isSubjectHeading(prev)) {
      subject = prev;
      break;
    }
    i -= 1;
  }

  return subject;
}

/**
 * 查询议题的末尾节点
 * @param controller
 * @param node
 * @returns
 */
export function getSubjectEndBlock(controller: Controller, node: SubjectHeading) {
  const { document } = controller.value;
  const path = document.getPath(node.key);
  if (!path) {
    return null;
  }
  const [index] = path;

  let endBlock: Block = node;
  let i = index + 1;
  while (i < document.nodes.length) {
    const next = document.nodes[i];
    if (isSubjectHeading(next)) {
      break;
    }
    endBlock = next;
    i += 1;
  }

  return endBlock;
}

/**
 * 查询议题是否包含聚合子节点
 * @param controller
 * @param node
 */
export function hasSubjectGroupBlocks(controller: Controller, node: SubjectHeading) {
  const { document } = controller.value;
  const nextBlock = document.getNextBlock(node.key);

  return !!nextBlock && !isSubjectHeading(nextBlock);
}
